.contact-us {
  color: #000;
  background-color: #fff;
  min-height: 100vh;
  padding: 25vh 5vw 10vh;
  font-family: Roboto, sans-serif;
}

.contact-us-container {
  max-width: 1200px;
  margin: 0 auto;
}

.contact-us-header {
  text-align: center;
  margin-bottom: 4rem;
}

.contact-us-header h1 {
  margin-bottom: 1rem;
  font-size: clamp(2rem, 5vw, 3.5rem);
}

.contact-us-header p {
  color: #000;
  max-width: 600px;
  margin: 0 auto;
  font-size: 1.2rem;
}

.contact-content {
  flex-direction: column;
  gap: 4rem;
  display: flex;
}

.contact-info-section h2, .location-section h2 {
  color: #1486dd;
  border-bottom: 2px solid #1486dd;
  margin-bottom: 2rem;
  padding-bottom: .5rem;
  font-size: 2rem;
}

.contact-info-grid {
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  display: grid;
}

.info-card {
  text-align: center;
  background: #ffffff0d;
  border: 1px solid #ffffff1a;
  border-radius: 10px;
  padding: 2rem;
  transition: transform .3s, box-shadow .3s;
}

.info-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px #1486dd33;
}

.info-icon {
  margin-bottom: 1rem;
  font-size: 3rem;
}

.info-card h3 {
  color: #1486dd;
  margin-bottom: 1rem;
  font-size: 1.3rem;
}

.info-card p {
  color: #000;
  margin: 0;
  line-height: 1.6;
}

.emergency-note {
  color: #ff6b6b;
  margin-top: 1rem;
  font-size: .9rem;
  font-weight: 600;
  display: block;
}

.location-section {
  background: #ffffff0d;
  border: 1px solid #ffffff1a;
  border-radius: 10px;
  padding: 2rem;
}

.location-content {
  grid-template-columns: 1fr;
  align-items: start;
  gap: 3rem;
  display: grid;
}

.location-details h3 {
  color: #1486dd;
  margin-bottom: 1rem;
  font-size: 1.4rem;
}

.location-details p {
  color: #000;
  margin-bottom: 2rem;
  line-height: 1.6;
}

.location-details ul {
  color: #000;
  margin-bottom: 2rem;
  padding-left: 1.5rem;
}

.location-details li {
  margin-bottom: .5rem;
}

.contact-cta {
  background: #1486dd1a;
  border-left: 4px solid #1486dd;
  border-radius: 8px;
  margin-top: 2rem;
  padding: 2rem;
}

.contact-cta h3 {
  color: #1486dd;
  margin-bottom: 1rem;
}

.contact-cta p {
  color: #000;
  margin-bottom: 1.5rem;
}

.cta-buttons {
  flex-wrap: wrap;
  gap: 1rem;
  display: flex;
}

.cta-btn {
  text-align: center;
  border-radius: 5px;
  min-width: 120px;
  padding: .8rem 1.5rem;
  font-weight: 600;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.cta-btn.primary {
  color: #fff;
  background: linear-gradient(135deg, #1486dd, #054b81);
}

.cta-btn.primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px #1486dd4d;
}

.cta-btn.secondary {
  color: #1486dd;
  background: none;
  border: 2px solid #1486dd;
}

.cta-btn.secondary:hover {
  color: #fff;
  background: #1486dd;
}

.map-placeholder {
  background: #ffffff0d;
  border: 2px dashed #fff3;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  min-height: 300px;
  display: flex;
}

.map-content {
  text-align: center;
  color: #000;
}

.map-icon {
  opacity: .7;
  margin-bottom: 1rem;
  font-size: 4rem;
}

.map-content p {
  margin-bottom: .5rem;
  font-size: 1.2rem;
}

.map-content small {
  color: #888;
}

@media (width <= 768px) {
  .contact-content {
    gap: 2rem;
  }

  .contact-us {
    padding: 5vh 2vw;
  }

  .contact-us-header {
    margin-bottom: 2rem;
  }

  .contact-us-header h1 {
    font-size: 2.5rem;
  }

  .contact-us-header p {
    font-size: 1rem;
  }

  .contact-info-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .location-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .info-card {
    padding: 1.5rem;
  }

  .cta-buttons {
    flex-direction: column;
  }

  .cta-btn {
    width: 100%;
  }
}

@media (width <= 480px) {
  .contact-us-header h1 {
    font-size: 2rem;
  }

  .contact-info-section h2, .location-section h2 {
    font-size: 1.5rem;
  }

  .info-card h3 {
    font-size: 1.1rem;
  }
}
